import { createAsyncThunk, createSlice } from '@reduxjs/toolkit'
import { fetchRanks } from '../api/ranks'
import { RootState } from '../app/store'
import { RankModel } from '../model/rankModel'
import { ResultModel } from '../model/resultModel'

export interface MeoState {
  ranks: ResultModel<RankModel>;
  fetching: boolean;
  error: boolean;
}

const initialState:MeoState = {
  ranks: {},
  fetching: false,
  error: false
}

export const getRanks = createAsyncThunk(
  'getRanks',
  async (value:any) => {
    const response = await fetchRanks(value)
    return response.data || []
  }
)

export const meoSlice = createSlice({
  name: 'meo',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(getRanks.pending, (state) => {
        state.fetching = true
      })
      .addCase(getRanks.fulfilled, (state, action) => {
        state.ranks = action.payload
        state.fetching = false
      })
      .addCase(getRanks.rejected, (state) => {
        state.fetching = false
        state.error = true
      })
  }
})

export const meo = (state: RootState) => state.meo

export default meoSlice.reducer
