export const apiUrl = () => {
  return process.env.REACT_APP_API_URL
}

const getHeaders = () => {
  return {
    'Content-Type': 'application/json'
  }
}

export const headers = getHeaders()

// Ranks
export const ranksEndpoint = (value: string) => value ? `${apiUrl()}/redbull/getRank/${value}` : `${apiUrl()}/redbull/getRank`
